import { Box, Divider } from "@mui/material";
import styled from "@emotion/styled";

import Keg from "./Keg";
import KegeratorHeader from "./KegeratorHeader";

const StyledBox = styled(Box)`
  width: 100%;
  border: solid #2c3646 2px;
  border-radius: 15px;
  background-color: #1e2938;
  color: white;
  padding: 10px;
`;

const StyledDivider = styled(Divider)`
  background-color: #364153;
  margin-top: 10px;
  margin-bottom: 10px;
`;

// TODO figure out how to handle kegsbox overflow

const KegsBox = styled(Box)`
  display: flex;
  gap: 20px;
`;

const KegeratorBox = ({ kegerator }) => {
  return (
    <StyledBox>
      <KegeratorHeader kegerator={kegerator} />
      <StyledDivider variant="middle" />
      <KegsBox>
        {kegerator.kegs.map((keg) => (
          <Keg key={keg.id} keg={keg} />
        ))}
      </KegsBox>
    </StyledBox>
  );
};

export default KegeratorBox;
