import { Box, Typography } from "@mui/material";
import { Circle } from "@mui/icons-material";
import styled from "@emotion/styled";
import { useState } from "react";

const getStatusColor = (status) => {
  if (status === true) return "#00c850";
  if (status === false) return "red";
  return "orange";
};

const StyledBox = styled(Box)`
  display: flex;
  justify-content: space-between;
`;

const NameBox = styled(Box)`
  display: flex;
  justify-content: start;
`;

const LocationTypography = styled(Typography)`
  opacity: 70%;
`;

const TempTypography = styled(Typography)`
  color: #4181cd;
  background-color: #111828;
  border-radius: 5px;
  border: solid #364153 2px;
  font-weight: bold;
  padding-left: 10px;
  padding-right: 10px;
`;

const TempStatusBox = styled(Box)`
  display: flex;
  align-items: center;
  gap: 10px;
`;

const StatusIcon = styled(Circle)`
  color: ${(props) => props.statusColor};
`;

const KegeratorHeader = ({
  kegerator: { name, location, temperature, error = false },
}) => {
  const [status, setStatus] = useState(!error);

  return (
    <StyledBox>
      <NameBox>
        <Typography variant="h5">{name}</Typography>
        <LocationTypography variant="h6">
          &nbsp;&nbsp;-&nbsp;&nbsp;{location}
        </LocationTypography>
      </NameBox>
      <TempStatusBox>
        <TempTypography variant="button">{temperature}&deg;F</TempTypography>
        <StatusIcon fontSize="small" statusColor={getStatusColor(status)} />
      </TempStatusBox>
    </StyledBox>
  );
};

export default KegeratorHeader;
