import { useMemo } from "react";
import { useLocation } from "react-router-dom";

/**
 * Hook that returns a numeric tab index based on the current path.
 * - overview -> 0
 * - history  -> 1
 * - not found -> -1
 */
export default function useGetTab() {
  const { pathname } = useLocation();

  const tabIndex = useMemo(() => {
    // normalize and split path into segments
    const segments = pathname.split("/").filter(Boolean);
    // prefer the last segment but fall back to any segment match
    const last = segments[segments.length - 1] || "";

    if (last === "overview" || segments.includes("overview")) return 0;
    if (last === "history" || segments.includes("history")) return 1;
    return -1;
  }, [pathname]);

  return tabIndex;
}
