import styled from "@emotion/styled";
import { useModal } from "../contexts/ModalContext";
import {
  Box,
  Typography,
  LinearProgress,
  linearProgressClasses,
} from "@mui/material";

const StyledBox = styled(Box)`
  padding: 20px;
  background-color: #111828;
  border: solid #2c3646 2px;
  border-radius: 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  transition: border-color 160ms ease, box-shadow 160ms ease,
    transform 160ms ease;

  &:hover {
    border-color: orange;
    box-shadow: 0 6px 18px rgba(255, 165, 0, 0.18),
      0 0 6px rgba(255, 165, 0, 0.08);
    transform: translateY(-3px);
  }
`;

const ProgressBox = styled(Box)`
  height: 250px;
`;

const StyledLinearProgress = styled(LinearProgress)`
  height: 100%;
  width: 100px;
  background-color: black;
  border-radius: 15px;
  border: solid #364153 3px;
  .${linearProgressClasses.bar} {
    background-color: orange;
    border-radius: 12px;
    transform: translateY(${(props) => props.volume}%) !important;
  }
  .${linearProgressClasses.colorPrimary} {
    border-radius: 15px;
  }
`;

const VolumeTypography = styled(Typography)`
  color: orange;
`;

const InfoTextBox = styled(Box)`
  display: flex;
  flex-direction: column;
  align-items: center;
`;

const VolumeBox = styled(Box)`
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5px;
`;

const getPercentage = (remainingVolume, totalVolume) => {
  const actual = Math.round((remainingVolume / totalVolume) * 10000) / 100;
  const pretty = Math.round(actual);
  return { actual, pretty };
};

const getTenthsPlace = (number) => {
  return Math.floor(number * 10) / 10;
};

const Keg = ({ keg }) => {
  const { openModal } = useModal();

  const handleKegClick = (e) => {
    console.log("Keg clicked:", keg);
    // open the global modal and pass the keg as modalProps
    openModal({ type: "keg", keg });
  };

  // TODO - replace today data with actual total_flow from api

  return (
    <StyledBox onClick={handleKegClick}>
      <Typography variant="h6">{keg.name}</Typography>
      <VolumeBox>
        <ProgressBox>
          <StyledLinearProgress
            variant="determinate"
            value={
              100 - getPercentage(keg.remainingVolume, keg.maxVolume).actual
            }
            volume={
              100 - getPercentage(keg.remainingVolume, keg.maxVolume).actual
            }
          />
        </ProgressBox>
        <VolumeTypography variant="h6">
          {getPercentage(keg.remainingVolume, keg.maxVolume).pretty}%
        </VolumeTypography>
      </VolumeBox>
      <InfoTextBox>
        <Typography variant="p">
          {getTenthsPlace(keg.remainingVolume)} / {keg.maxVolume} gal
        </Typography>
        <Typography variant="p">
          Today: {getTenthsPlace(keg.maxVolume - keg.remainingVolume)} gals
        </Typography>
      </InfoTextBox>
    </StyledBox>
  );
};

export default Keg;
