import useEventMessages from "../hooks/useEventMessages";
import { Typography } from "@mui/material";
import styled from "@emotion/styled";

const MessagesHeader = styled(Typography)`
  color: #ffffff;
`;

const MessagesBody = styled(Typography)`
  color: #ffffff;
`;

function LiveData() {
  const { lastMessage, error } = useEventMessages();

  let display = "No Messages Yet";
  if (error) display = "EventSource error";
  else if (lastMessage != null) {
    display =
      typeof lastMessage === "string"
        ? lastMessage
        : JSON.stringify(lastMessage);
  }

  return (
    <div>
      <MessagesHeader variant="h1">Real-Time Messages</MessagesHeader>
      <MessagesBody variant="body1">{display}</MessagesBody>
    </div>
  );
}

export default LiveData;
