import { useState } from "react";
import { Link } from "react-router-dom";
import {
  AppBar,
  Toolbar,
  Typography,
  Box,
  IconButton,
  Menu,
  MenuItem,
  Tabs,
  Tab,
} from "@mui/material";
import { AccountCircle } from "@mui/icons-material";
import styled from "@emotion/styled";

import HeaderLogo from "./HeaderLogo";
import useGetTab from "../utils/getTab";

const StyledAppBar = styled(AppBar)`
  background-color: black;
  color: white;
  border-bottom: 1px solid #334155;
  display: flex;
`;

const FlexToolbar = styled(Toolbar)`
  display: flex;
  justify-content: space-between;
`;

const TitleDiv = styled.div`
  display: flex;
  align-items: center;
`;

const IconBox = styled(Box)`
  border-radius: 8px;
  padding: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 12px;
`;

const HeaderText = styled(Typography)`
  color: white;
`;

const MenuDiv = styled.div`
  display: flex;
  align-items: center;
`;

const LinkTab = (props) => {
  return (
    <Tab component={Link} aria-current={props.selected && "page"} {...props} />
  );
};

const Header = () => {
  const [anchorEl, setAnchorEl] = useState(null);
  const tabValue = useGetTab();

  const handleMenu = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleMenuClose = () => {
    setAnchorEl(null);
  };

  return (
    <StyledAppBar position="static" elevation={0}>
      <FlexToolbar>
        <TitleDiv>
          <IconBox>
            <HeaderLogo />
          </IconBox>
          <Box>
            <HeaderText variant="body2" color="text.secondary">
              Keg Monitoring System
            </HeaderText>
          </Box>
        </TitleDiv>
        <MenuDiv>
          <Tabs
            value={tabValue}
            textColor="inherit"
            indicatorColor="primary"
            role="navigation"
          >
            <LinkTab label="Overview" to="/overview" />
            <LinkTab label="History" to="/history" />
          </Tabs>
          <IconButton
            size="large"
            aria-label="account of current user"
            aria-controls="menu-appbar"
            aria-haspopup="true"
            onClick={handleMenu}
            color="inherit"
          >
            <AccountCircle />
          </IconButton>
          <Menu
            id="menu-appbar"
            anchorEl={anchorEl}
            anchorOrigin={{
              vertical: "top",
              horizontal: "right",
            }}
            keepMounted
            transformOrigin={{
              vertical: "top",
              horizontal: "right",
            }}
            open={Boolean(anchorEl)}
            onClose={handleMenuClose}
          >
            <MenuItem onClick={handleMenuClose}>Profile</MenuItem>
            <MenuItem onClick={handleMenuClose}>My account</MenuItem>
          </Menu>
        </MenuDiv>
      </FlexToolbar>
    </StyledAppBar>
  );
};

export default Header;
