import { useCallback, useEffect, useRef, useState } from "react";

/**
 * useEventMessages
 * Connects to an SSE endpoint and accumulates incoming messages.
 *
 * @returns {{ messages: Array, lastMessage: any, error: any, close: function }}
 */
export default function useEventMessages() {
  const [messages, setMessages] = useState([]);
  const [error, setError] = useState(null);
  const esRef = useRef(null);

  useEffect(() => {
    setError(null);
    setMessages([]);

    let mounted = true;
    try {
      const es = new EventSource(
        `${process.env.REACT_APP_EVENT_SOURCE_URL}/events`
      );
      esRef.current = es;

      es.onmessage = (event) => {
        if (!mounted) return;
        let payload = event.data;
        try {
          payload = JSON.parse(event.data);
        } catch (e) {
          // not JSON, keep raw string
        }
        setMessages((prev) => [...prev, payload]);
      };

      es.onerror = (err) => {
        if (!mounted) return;
        setError(err);
      };
    } catch (err) {
      setError(err);
    }

    return () => {
      mounted = false;
      if (esRef.current) {
        esRef.current.close();
        esRef.current = null;
      }
    };
  }, []);
  const close = useCallback(() => {
    if (esRef.current) {
      esRef.current.close();
      esRef.current = null;
    }
  }, []);

  return {
    messages,
    lastMessage: messages.length ? messages[messages.length - 1] : null,
    error,
    close,
  };
}
