import { useEffect, useState } from "react";

import { Container } from "@mui/material";
import Header from "../components/Header";
import KegeratorBox from "../components/KegeratorBox";
import styled from "@emotion/styled";

import useGetTab from "../utils/getTab";
import useEventMessages from "../hooks/useEventMessages";
import KegModal from "../components/KegModal";

const StyledContainer = styled(Container)`
  padding: 20px;
  max-width: 1220px;
`;

const Body = () => {
  const tabIndex = useGetTab();
  const { lastMessage, error } = useEventMessages();
  const [kegerators, setKegerators] = useState([]);

  useEffect(() => {
    if (Array.isArray(lastMessage)) {
      setKegerators(lastMessage);
    }
  }, [lastMessage, error]);

  if (tabIndex === 0) {
    return (
      <>
        <StyledContainer maxWidth={false}>
          {kegerators.map((kegerator) => (
            <KegeratorBox kegerator={kegerator} />
          ))}
        </StyledContainer>
        <KegModal />
      </>
    );
  }
  if (tabIndex === 1) {
    return null;
  }
};

const Index = () => {
  return (
    <>
      <Header />
      <Body />
    </>
  );
};

export default Index;
