import { Box, Modal, Card, CardHeader } from "@mui/material";
import styled from "styled-components";
import { useModal } from "../contexts/ModalContext";

const ModalBox = styled(Box)`
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: solid #2c3646 2px;
  border-radius: 15px;
  background-color: #1e2938;
  padding: 16px;
`;

const ModalCard = styled(Card)`
  position: absolute;
  top: 50%;
  left: 50%;
`;

const KegModal = () => {
  const { isOpen, closeModal, modalProps } = useModal();

  // TODO - populate modal with keg data
  // pressure
  // general description
  // day tapped - time lapsed
  // flavor notes
  // size of keg
  // onTap api

  const handleClose = () => {
    closeModal();
  };
  return (
    <Modal open={isOpen} onClose={handleClose}>
      <ModalCard>
        <CardHeader title={modalProps?.keg?.name} />
      </ModalCard>
    </Modal>
  );
};

export default KegModal;
