import { use, useEffect } from "react";
import "./App.css";
import { BrowserRouter, HashRouter, Routes, Route, Navigate, useLocation, useNavigate } from "react-router-dom";

import Index from "./pages/Index";
import LiveData from "./pages/LiveData";

function Router({children}) {
  const useHashRouter = process.env.REACT_APP_USE_HASH_ROUTER; // Set to true to use HashRouter
  if (useHashRouter === "true") {
    return <HashRouter>{children}</HashRouter>;
  }
  return <BrowserRouter>{children}</BrowserRouter>;
}

function PageRoutes({children}) {
  const location = useLocation();
  const navigate = useNavigate();

  useEffect(() => {
    if (location.pathname === "/") {
    console.log("Redirecting to /overview");
    const useHashRouter = process.env.REACT_APP_USE_HASH_ROUTER; // Set to true to use HashRouter
    if (useHashRouter === "true") {
      navigate(`/overview`);
    }
    navigate(`/overview`);
  }
  }, [location]);

  

  return (
    <Routes>
      {children}
    </Routes>
  );
}

function App() {
  const ENVIRONMENT = process.env.REACT_APP_NODE_ENV || "dev";
  
  let baseUrlPath = "";
  console.log("REACT ENVIRONMENT:", ENVIRONMENT);
  console.log("REACtT APP ENVIRONMENT:", process.env.REACT_APP_NODE_ENV);
  console.log("process env", process.env);
  if (ENVIRONMENT === "prod") {
    baseUrlPath = "/intellitap_web_app_build";
  }

  // TODO - separate tab for flow history graph
  return (
    <Router basename={baseUrlPath}>
      <PageRoutes baseUrlPath={baseUrlPath}>
        <Route path="*" element={<Navigate to="/overview" replace />} />
        <Route path="/overview" element={<Index />} />
        <Route path="/history" element={<Index />} />
        <Route path="/data" element={<LiveData />} />
      </PageRoutes>
    </Router>
  );
}

export default App;
